<template lang="html">
  <div class="">
      <div class="cli-header">
        <div class="logo"><h1>wwvue-cli</h1></div>
        <div class="nav">
          <li><a href="#"></a></li>
          <li><a href="#"></a></li>
          <li><a href="#"></a></li>
        </div>
      </div>
      <div class="cli-main">
          <div class="intro" id="">
            <h1>Vue.js</h1>
            <h2>  </h2>
            <div class="btn-item">
              <button @click="locationPage('https://github.com/vannvan/wvue-cli')"></button>
              <button @click='locationPage("https://github.com/vannvan/wwvue-cli/issues/1")'></button>
            </div>
            
          </div>
          <div class="features" id="">
            <div class="title"></div>
            <div class="list">
              <div class="item-col-3">
                <h3></h3>
                Axios,Vuex,Router
              </div>
              <div class="item-col-3">
                <h3></h3>
                mixins,derective,scss,
              </div>
              <div class="item-col-3">
                <h3></h3>
                ,
              </div>
            </div>
          </div>
          <div class="issues" id="">
            <div class="title"></div>
            <div class="list">
              <div class="item-col-2">
                <p>1</p>
                <p>distnpm run packwwvue-cli_090945.zip<a v-danger href="https://github.com/vannvan/wvue-cli/tree/1.x"></a></p>
              </div>
              <div class="item-col-2">
                <p>2</p>
                <p>distbuild</p>
              </div>
              <div class="item-col-2">
                <p>3Vuex</p>
                <p>Vuexstoremain.jsbaseInfo</p>
                <p>main.js,modules</p>
                <pre>
    modules: {
      baseInfoStore: baseInfo_store,
      //
    },
                </pre>
              </div>
            </div>
          </div>
      </div>
      <div class="cli-footer">
          author@ vannvan https://github.com/vannvan
      </div>
  </div>
</template>

<script>
export default {
  methods: {
    locationPage(location) {
      window.open(location)
    }
  }
}
</script>

<style lang="scss">
*{
  color: #fff;
}
h1{
  font-size: 50px;
  text-align: center;
}
h2{
  text-align: center;
}
.cli-header{
  height: 80px;
  width: 100%;
  background: #00649F;
  line-height: 80px;
  display: flex;
  justify-content: space-between;
  color: #f6f6f6;
  .logo{
    width: 30%;
    margin-left: 10%;
  }
  .nav{
      margin-right: 10%;
      li{
        list-style: none;
        float: left;
        padding: 0 50px;
        font-weight: bold;
        cursor: pointer;
        a{
          text-decoration: none
        }
      }
  }
}
.cli-main{
  width: 100%;
  background: #01AAC1;
  .intro{
    height: 400px;
    padding-top:100px;
    color: #fff;
    h2{
      text-align: center;
      margin-top: 35px
    }
    .btn-item{
      width: 400px;
      height: 50px;
      margin:0 auto;
      display: flex;
      justify-content: space-between;
      margin:25px auto;
        button{
          width: 150px;
          height: 50px;
          // margin: 25px auto;
          display: block;
          text-align: center;
          line-height: 50px;
          border:#f6f6f6 solid 1px;
          border-radius: 5px;
          cursor: pointer;
          background: none
          // float: left
      }
    }
    
  }
  .features{
    background: #00649F;
    height: auto;
    padding-bottom: 35px;
    padding-top: 50px;
    .title{
      height: 80px;
      text-align: center;
      font-size: 35px;
      line-height: 80px;
    }
    .list{
      display: flex;
      // padding-left: 10%;
      .item-col-3{
        width: 30%;
        margin:0 5%;
        height: 180px;
        padding: 35px;
        // background: #f00;
        line-height: 45px;
        h3{
          line-height: 60px;
        }
      }
    }
  }
  .issues{
    background: #01AAC1;
    padding-bottom: 35px;
    padding-top: 50px;
    height: auto;
    .title{
      height: 80px;
      text-align: center;
      font-size: 35px;
      line-height: 80px;
    }
    .list{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      .item-col-2{
        width: 36%;
        margin:25px 5%;
        height: 180px;
        padding:35px;
        p{
          line-height: 45px;
        }
      }
    }
  }
}
.cli-footer{
  height: 60px;
  width: 100%;
  background: #00649F;
  text-align: center;
  line-height: 60px;
}
</style>



